<template>
  <div class="loginbox">
    <mu-scale-transition>
    <div style="font-size: 50px; margin-bottom: 20px" ref="logintext">你好</div>
    </mu-scale-transition>
    <div style="display:flex;justify-content:center;align-items:center;flex-direction:column">
            <mu-text-field
        v-model="classnum"
        placeholder="考号"
        :disabled="is_loading"
      ></mu-text-field>
      <br>
      <mu-text-field
        v-model="pwd"
        placeholder="密码"
        :disabled="is_loading"
      ></mu-text-field>
      <br>
      <mu-button color="primary" v-loading="is_loading" data-mu-loading-size="24" :disabled="is_loading" @click="submitloading">登录</mu-button>
   
    </div>
  </div>
</template>

<script>
export default {
  name: "loginpage",
  components: {},
  data() {
    return {
      is_loading: false,
      classnum: "",
      pwd: "",
    };
  },
  methods: {
    setCookie: function (cname, cvalue, exdays) {
      var d = new Date();
      d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
      var expires = "expires=" + d.toUTCString();
      console.info(cname + "=" + cvalue + "; " + expires);
      document.cookie = cname + "=" + cvalue + "; " + expires;
      console.info(document.cookie);
    },
    submitloading() {
            const loading = this.$loading();
      this.$loading()
      let data = {
        classnum: this.$data.classnum,
        pwd: this.$data.pwd,
      };
      this.$axios({
        method: "post",
        url: "https://log-ena97-1302415181.ap-shanghai.app.tcloudbase.com/vueecho",
        header: {
          "Content-Type": "application/json", //如果写成contentType会报错,如果不写这条也报错
          //Content type 'application/x-www-form-urlencoded;charset=UTF-8'...
        },
        data: data,
        withCredentials: true,
      }).then((res) => {
        loading.close()
        if (res.data.type == "notice") {
          this.$refs.logintext.innerText = "账号或密码错误";
          this.$refs.logintext.style.color = "darkred";
          this.$data.is_loading = false;
          setTimeout(() => {
            this.$refs.logintext.innerText = "登录";
            this.$refs.logintext.style.color = "";
          }, 5000);
        }
        if (res.data.type == "token") {
          this.$router.push("/index");
          this.setCookie("jtoken", res.data.context, 1);
        }
      });
    },
  },
};
</script>

<style scoped>
.flex-wrapper {
  width: 100%;
  height: 56px;
  margin-top: 8px;
  flex-direction: column;
}
.loginbox {
  justify-content: center;
}
.inputbox {
  justify-content: center;

  display: flex;
  flex-direction: column;
  /* margin: 0 auto; */
}
</style>